/**
 * Import variables from the specified stylesheet.
 */
@import "./styles/variables.css";

/**
  * Styles for the main application container.
  */
.App {
  font-family: "DM Sans", sans-serif;
  background-color: var(--background-secondary-light);
  color: var(--text-color-light);
  min-height: 100vh;
}

/**
  * Dark mode styles for the main application container.
  */
.dark.App {
  background-color: var(--background-secondary-dark);
}

/**
  * Styles for the main content container.
  */
.Main {
  padding-top: 81px;
}

/**
  * Styles for the content area.
  */
.Content {
  background-color: var(--background-secondary-light);
  color: var(--text-color-light);
  box-shadow: 8px 8px 8px 8px var(--box-shadow);
  padding: 10px;
  border-radius: 10px;
  border: 1px solid var(--background-secondary-light);
}

/**
  * Dark mode styles for the content area.
  */
.dark.Content {
  background-color: var(--background-secondary-dark);
  color: var(--text-color-dark);
  box-shadow: 8px 8px 8px 8px var(--box-shadow);
  border: 1px solid var(--background-secondary-dark);
}

/**
  * Styles for the filler image.
  */
.FillerImage {
  object-fit: scale-down;
  height: 100%;
  width: 100%;
}

/**
  * Styles for general images.
  */
.Image {
  max-height: 40vh;
  max-width: 100%;
  object-fit: cover;
}

/**
  * Box-sizing property applied to all elements.
  */
* {
  box-sizing: border-box;
}

/**
  * Styles for hyperlinks.
  */
a {
  color: var(--text-color-muted);
  transition: color ease-in-out 0.3s;
}

/**
  * Styles for file input elements.
  */
input[type="file"] {
  display: none;
}

/**
  * Styles for input elements.
  */
.Input {
  background: var(--background-secondary-light);
  border: 1px solid var(--text-color-light);
  box-sizing: border-box;
  border-radius: 20px;
  color: var(--text-color-light);
  text-align: center;
  padding: 10px;
}

/**
  * Dark mode styles for input elements.
  */
.dark.Input {
  background: var(--background-secondary-dark);
  color: var(--text-color-dark);
  border: 1px solid var(--text-color-dark);
}

/**
  * Hover styles for hyperlinks.
  */
a:hover {
  text-decoration: none;
  color: var(--brand-color);
}

/**
  * Hover styles for hyperlinks with icons.
  */
a:hover i {
  color: var(--brand-color);
  transition: color ease-in-out 0.3s;
}

/**
  * Styles for icon elements.
  */
i {
  color: var(--icon-color);
  font-size: 1.1rem;
  padding: 7px;
  vertical-align: baseline;
}

/**
  * Hover styles for icons.
  */
i:hover {
  color: var(--brand-color);
  cursor: pointer;
  transition: color ease-in-out 0.3s;
}

/**
  * Styles for horizontal lines.
  */
.HrLine {
  border-color: var(--text-color-muted);
}

/**
  * Dark mode styles for horizontal lines.
  */
.dark.HrLine {
  border-color: var(--text-color-light);
}

/**
  * Styles for figure elements.
  */
figure {
  text-align: center;
  display: inline-block;
}

/**
  * Scrollbar styles.
  */
/* scrollbar */
*::-webkit-scrollbar {
  width: 5px;
}

/**
  * Track styles for the scrollbar.
  */
*::-webkit-scrollbar-track {
  background: var(--background-light);
}

/**
  * Thumb styles for the scrollbar.
  */
*::-webkit-scrollbar-thumb {
  background: var(--text-color-light);
}

/**
  * Hover styles for the scrollbar thumb.
  */
*::-webkit-scrollbar-thumb:hover {
  background: var(--text-secondary-color-light);
}
